@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Martel+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.reax-app {
  font-family: "Martel Sans", sans-serif; }

h1, h2, h3 {
  font-family: "Abril Fatface", cursive;
  color: #2b2d42; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.dark h1, .dark h2, .dark h3 {
  color: #fff2f1; }

.reax-auth {
  height: 100vh;
  background-size: cover;
  color: #8d99ae; }
  .reax-auth-wrapper {
    height: 100vh;
    background: white;
    display: flex;
    justify-content: space-between;
    flex-direction: column; }
  .reax-auth-selection-type {
    padding-bottom: 13px;
    margin: 15px 0;
    border-bottom: 1px solid #e5d8d7; }
    .reax-auth-selection-type a {
      color: #8d99ae;
      padding: 8px 20px; }
      .reax-auth-selection-type a:hover {
        color: #2b2d42;
        cursor: pointer; }
      .reax-auth-selection-type a.active {
        color: #2b2d42;
        border-bottom: 2px solid #006DFF;
        position: relative; }
        .reax-auth-selection-type a.active:before {
          position: absolute;
          bottom: 0;
          left: calc(50% - 4px);
          content: '';
          width: 8px;
          height: 8px;
          border: 8px solid transparent;
          border-bottom-color: #006DFF; }
      .reax-auth-selection-type a:hover {
        text-decoration: none; }

.reax-login h1 {
  margin-bottom: 15px; }

.reax-login p {
  margin-bottom: 30px; }

.reax-login .reax-btn {
  margin-top: 15px; }

.reax-input {
  border: none;
  border-radius: 30px;
  outline: none;
  padding: 8px 15px;
  display: flex;
  width: 100%;
  margin-bottom: 15px;
  background-color: #fff2f1;
  color: #2b2d42;
  border: 1px solid #fff2f1;
  -webkit-transition: .25s all ease;
  transition: .25s all ease; }
  .reax-input:focus {
    border: 1px solid #ffd6d2; }

.reax-btn {
  border: none;
  outline: none;
  color: white;
  padding: 8px 20px;
  border-radius: 30px;
  box-shadow: 0 3px 6px #004cb3;
  -webkit-transition: all ease .5s;
  transition: all ease .5s; }
  .reax-btn:hover {
    background: #0057cc; }
  .reax-btn:active {
    background: #0057cc;
    box-shadow: none; }

.reax-sign-in h1 {
  margin-bottom: 15px; }

.reax-sign-in p {
  margin-bottom: 30px; }

.reax-sign-in .reax-btn {
  margin-top: 15px; }

.julls-app-credits {
  position: relative;
  margin-right: 15px;
  margin-bottom: 15px; }
  .julls-app-credits img {
    width: 50px;
    border-radius: 50%; }
  .julls-app-credits::before {
    display: none;
    content: 'Hecho con <3 por Jullietv';
    position: absolute;
    background: #4a4a4a;
    color: #fff;
    padding: 2px 8px;
    border-radius: 2px;
    top: calc(50% - 9px);
    width: 186px;
    font-size: .8rem;
    font-family: 'Verdana', sans-serif;
    left: 65px; }
  .julls-app-credits:hover:before {
    display: block; }

/**
 * Blog
 * @version v1.0.0
 */
.reax-blog {
  min-height: 100vh; }
  .reax-blog-wrapper {
    padding: 100px 20px 0 20px; }
  .reax-blog .left-align {
    float: left;
    margin-right: 15px; }
  .reax-blog .right-align {
    float: right;
    margin-left: 15px; }
  .reax-blog .full-width {
    width: 100%;
    display: block; }
  .reax-blog img {
    max-width: 100%;
    margin-bottom: 15px;
    -webkit-transition: .5s all ease;
    transition: .5s all ease; }
    .reax-blog img.right-align {
      box-shadow: 7px 7px 0 #006DFF; }
    .reax-blog img.left-align {
      box-shadow: -7px 7px 0 #006DFF; }
    .reax-blog img.full-width {
      box-shadow: 7px 7px 0 #006DFF; }

.dark .reax-blog {
  background-color: #2b2d42; }

/**
 * Navbar
 * @version v1.0.0
 */
.reax-navbar {
  background: white;
  width: 100%;
  position: fixed;
  left: 0;
  z-index: 999; }
  .reax-navbar-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e5d8d7; }
  .reax-navbar-brand {
    padding: 0 20px; }
    .reax-navbar-brand h1 {
      color: #006DFF; }
  .reax-navbar-menu ul {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    margin-bottom: 0; }
    .reax-navbar-menu ul a {
      color: #8d99ae;
      padding: 15px 45px;
      text-transform: uppercase;
      letter-spacing: 2px; }
      .reax-navbar-menu ul a:hover {
        color: #2b2d42;
        cursor: pointer; }
      .reax-navbar-menu ul a.active {
        color: #2b2d42;
        border-bottom: 2px solid #006DFF;
        position: relative;
        font-weight: 600;
        color: #006DFF; }
        .reax-navbar-menu ul a.active:before {
          position: absolute;
          bottom: 0;
          left: calc(50% - 4px);
          content: '';
          width: 8px;
          height: 8px;
          border: 8px solid transparent;
          border-bottom-color: #006DFF; }
      .reax-navbar-menu ul a:hover {
        text-decoration: none; }
      .reax-navbar-menu ul a .fa, .reax-navbar-menu ul a .fab, .reax-navbar-menu ul a .fad, .reax-navbar-menu ul a .fal, .reax-navbar-menu ul a .far, .reax-navbar-menu ul a .fas {
        padding: 0 20px;
        font-size: 25px; }

@media screen and (max-width: 768px) {
  .reax-navbar-menu ul a .fa,
  .reax-navbar-menu ul a .fab,
  .reax-navbar-menu ul a .fad,
  .reax-navbar-menu ul a .fal,
  .reax-navbar-menu ul a .far,
  .reax-navbar-menu ul a .fas {
    padding: 0 8px;
    font-size: 25px; } }

.dark .reax-navbar {
  background: #2b2d42; }
  .dark .reax-navbar-brand h1 {
    color: #fff2f1; }
  .dark .reax-navbar-menu ul a:not(.active) {
    color: #8d99ae; }
    .dark .reax-navbar-menu ul a:not(.active):hover {
      color: #e5d8d7; }

/**
 * Footer
 * @version v1.0.0
 */
.reax-footer {
  margin-top: 45px;
  padding: 15px 20px;
  border-top: 1px solid #e5d8d7;
  display: flex;
  align-items: center; }

.dark {
  color: #fff2f1; }

/**
 * Post
 * @version v1.0.0
 */
.reax-post {
  color: #8d99ae; }
  .reax-post-header h2 {
    position: relative;
    z-index: 3;
    display: inline; }
    .reax-post-header h2:before {
      content: '';
      opacity: .8;
      z-index: 1;
      position: absolute;
      bottom: 1px;
      right: -4px;
      display: block;
      width: 40px;
      height: 8px;
      background: #006DFF; }
  .reax-post-comments {
    margin-top: 30px;
    padding-top: 15px;
    border-top: 1px solid #e5d8d7; }
    .reax-post-comments h3 {
      position: relative;
      z-index: 3;
      display: inline; }
      .reax-post-comments h3:before {
        content: '';
        opacity: .8;
        z-index: 1;
        position: absolute;
        bottom: 1px;
        right: -4px;
        display: block;
        width: 40px;
        height: 6px;
        background: #006DFF; }
    .reax-post-comments-create {
      margin-bottom: 45px;
      display: flex;
      justify-content: space-between; }
      .reax-post-comments-create textarea {
        width: calc(100% - 65px);
        border-radius: 30px 0 30px 30px;
        border: 0;
        resize: none;
        padding: 15px;
        background-color: #ffe4e2;
        margin-top: 25px;
        position: relative; }
        .reax-post-comments-create textarea:focus {
          border: 1px solid #e5d8d7; }

/**
 * Avatar
 * @version v1.0.0
 */
.reax-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  background: #fff2f1;
  color: #ff6458;
  position: relative;
  border: 2px solid #ff6458; }
  .reax-avatar img {
    margin-bottom: 0;
    min-height: 50px;
    max-width: none; }

/**
 * Comment
 * @version v1.0.0
 */
.reax-comment {
  margin: 30px 0;
  display: flex;
  justify-content: space-between; }
  .reax-comment-dialog {
    width: calc(100% - 65px);
    margin-top: 25px;
    border-radius: 0 30px 30px 30px;
    border: 0;
    resize: none;
    padding: 15px;
    background-color: #fff2f1;
    margin-top: 25px;
    position: relative; }
    .reax-comment-dialog-header {
      margin-bottom: 15px; }
      .reax-comment-dialog-header h5 {
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 1rem; }
    .reax-comment-dialog-footer {
      margin-top: 15px;
      font-size: .8rem; }

/**
 * SiteConfig
 * @version v1.0.0
 */
.reax-site-config {
  position: fixed;
  top: 50%;
  left: 15px; }
  .reax-site-config ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .reax-site-config ul a {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: .8rem; }
      .reax-site-config ul a i {
        font-size: 25px;
        margin-bottom: 7px; }

.dark .reax-site-config li {
  color: white; }

